@use "global" as *;

.p-guide-mv {
  position: relative;
  padding-top: 129.7%;
  background-image: url(../images/common/guide-mv-sp.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin-top: rem(100);
  z-index: -1;
  @include mq(md) {
    /* height: (400 / 1400) * 100vw; */
    background-image: url(../images/common/guide-mv.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    padding-top: 24.28%;
    margin-top: rem(60);
    height: initial;
  }
}
.p-guide-mv__content {
  position: absolute;
  content: "";
  top: 23%;
  left: 50%;
  transform: translate(-50%, -50%);
  @include mq(md) {
    top: 50%;
    left: 40%;
  }
}
.p-guide-mv__point1,
.p-guide-mv__point2 {
  font-size: rem(54);
  font-weight: 700;
  position: absolute;
}
.p-guide-mv__point1 {
  top: -6%;
  left: -11%;
}
.p-guide-mv__point2 {
  top: 43%;
  right: 13%;
}
